<template>
  <div class>
    <div class="">
      <div class="m-1 d-inline" v-for="item in items" :key="uuid(item)">
        <img class=" lozad" :src="getItemURL(item.name)" width="50px" />
      </div>
    </div>
    <!-- <ItemInfo
      v-once
      :id="'itemInfo-' + r + getName(item.name)"
      :name="item.name"
      :type="isCombineOrInfo(item)"
       v-for="item in items" :key="uuid(item.name)"
    />-->
  </div>
</template>

<script>
import ItemInfo from "./ItemInfo";

export default {
  name: "Items",
  data() {
    return {
      r: this.uuid(1)
    };
  },
  components: {
    ItemInfo
  },
  created() {
    const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    observer.observe();
  },
  methods: {
    isCombineOrInfo: function(item) {
      return item.into ? "into" : "combine";
    }
  }
};
</script>

