<template>
  <div id="app" class="container-fluid">
    <div class="row">
      <div
        class="col-3 fixed-bottom"
        id="champList"
        style="border-right:1px solid gray; height: 100vh;overflow-y: auto"
      >
        <!-- <Champ class="card m-1 container-fluid" :champ="c"/> -->
        <div
          style=" display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;"
        >
          <nav class="tabs d-none d-xl-block bg-secondary text-white">
            <div class="selector"></div>
            <a
              class="text-white active"
              href="#"
              @click.stop.self="changeTab($event, 1)"
              title="Sort by name"
            >
              <i
                class="fas"
                :class="[alpha_up ? 'fa-sort-alpha-down' : 'fa-sort-alpha-down-alt']"
                @click="clickParent($event)"
              ></i>
            </a>
            <a
              class="text-white"
              href="#"
              @click="changeTab($event, 2)"
              title="Sort by origin"
            >Origins</a>
            <a
              class="text-white"
              href="#"
              @click="changeTab($event, 3)"
              title="Sort by class"
            >Classes</a>
            <a
              class="text-white"
              href="#"
              @click.stop.self="changeTab($event, 4)"
              title="Sort by cost"
            >
              <i
                class="fas"
                :class="[cost_up ? 'fa-sort-amount-down-alt' : 'fa-sort-amount-down']"
                @click="clickParent($event)"
              ></i>
            </a>
            <!-- <a href="#" @click="changeTab($event, true)">
              <i class="fas fa-search"></i>
            </a>-->
          </nav>
          <input class="search-input" type="text" v-model="inputSearch" @keyup="enterText()" />
        </div>
        <!-- <div
          class="demo m-1"
          v-for="c in listChamp.filter(c => c.set[0] == 2)"
          :key="c.id"
          :data-name="c.name"
          :data-set="c.set[0]"
          v-on:dragstart="drag($event)"
          :draggable="true"
        ></div>-->
        <div class="px-2">
          <ChampIcon
            class="m-1 champ-icon"
            :id="'icon-' + getName(c.name)"
            v-for="c in list.filter(c => c.set[0] == 2)"
            :champ="c"
            :draggable="true"
            :key="c.id"
            :showItems="false"
            :showInfo="false"
            :data-name="c.name"
          />
        </div>
      </div>
      <div class="offset-3"></div>
      <div class="col-7" style="height: 100%;over-flow:scroll">
        <ul class="nav nav-tabs">
          <li class="nav-item">
            <a class="nav-link active" data-toggle="tab" href="#teamComp" @click="tab = 1">Team Comp</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" data-toggle="tab" href="#teamBuilder" @click="tab = 2">Team Builder</a>
          </li>
        </ul>
        <div class="tab-content bg-dark">
          <div v-show="tab == 1" id="teamComp" class="list-group bg-dark">
            <TeamComp
              v-once
              class="container list-group-item"
              :team="team"
              :set="team.set[0]"
              v-for="team in teamComp"
              :key="uuid(team)"
            />
          </div>
          <div v-show="tab == 2" id="teamBuilder" class="bg-dark">
            <TeamComp class="container" />
          </div>
        </div>
      </div>
      <div class="col-2 p-0">
        <Items />
      </div>
    </div>
  </div>
</template>

<script>
import ChampIcon from "./components/ChampIcon";
import TeamComp from "./components/TeamComp";
import Items from "./components/Items";
import Loader from "./components/Loader";

// import data from './assets/data';
// import mixin from './assets/data'

export default {
  name: "App",
  components: {
    ChampIcon,
    TeamComp,
    Items,
    Loader
  },
  data() {
    return {
      list: [],
      tab: 1,
      inputSearch: "",
      debounce_timer: null,
      cost_up: false,
      alpha_up: false,
      origin_up: false,
      class_up: false,
      iconCount: window.iconCount,
      isShow: false
    };
  },
  created() {
    this.list = this.listChamp;
    this.teamComp = this.teamComp.sort((a, b) => a.tier - b.tier);
    window.iconCount = 0;
    // let that = this;
    // setTimeout(() => {
    //   $(".bg-loader").remove();
    // }, 5000);
  },
  mounted() {
    // setTimeout(() => {
    let activeItem = document.querySelector(".tabs .active");
    let activeWidth = activeItem.offsetWidth;
    document.querySelector(".selector").style.left = 0 + "px";
    document.querySelector(".selector").style.width = activeWidth + "px";
    // }, 5000);
    const observer = lozad(); // lazy loads elements with default selector as '.lozad'
    observer.observe();
  },
  methods: {
    drag: function(ev) {
      ev.dataTransfer.setData("name", ev.target.dataset.name);
      ev.dataTransfer.setData("set", ev.target.dataset.set);
    },
    clickParent: function(ev) {
      ev.target.parentElement.click();
    },
    changeTab: function(e, p) {
      console.log(e);
      let that = this;
      const sorting = {
        1: function() {
          that.list.sort((a, b) => {
            let nameA = a.name.toUpperCase(); // ignore upper and lowercase
            let nameB = b.name.toUpperCase(); // ignore upper and lowercase
            if (!that.alpha_up) {
              if (nameA < nameB) {
                return -1;
              }
              if (nameA > nameB) {
                return 1;
              }
            }
            // names must be equal
            return 0;
          });
          if (that.alpha_up) that.list.reverse();
          that.alpha_up = !that.alpha_up;
        },
        2: function() {
          that.list.sort((a, b) => {
            let nameA = a.origin[0].toUpperCase(); // ignore upper and lowercase
            let nameB = b.origin[0].toUpperCase(); // ignore upper and lowercase
            if (!that.origin_up) {
              if (nameA < nameB) {
                return -1;
              }
              if (nameA > nameB) {
                return 1;
              }
            }
            // names must be equal
            return 0;
          });
          if (that.origin_up) that.list.reverse();
          that.origin_up = !that.origin_up;
        },
        3: function() {
          that.list.sort((a, b) => {
            let nameA = a.type[0].toUpperCase(); // ignore upper and lowercase
            let nameB = b.type[0].toUpperCase(); // ignore upper and lowercase
            if (!that.class_up) {
              if (nameA < nameB) {
                return -1;
              }
              if (nameA > nameB) {
                return 1;
              }
            }
            // names must be equal
            return 0;
          });
          if (that.class_up) that.list.reverse();
          that.class_up = !that.class_up;
        },
        4: function() {
          if (that.cost_up) {
            that.list.sort((a, b) => b.cost - a.cost);
          } else {
            that.list.sort((a, b) => a.cost - b.cost);
          }
          that.cost_up = !that.cost_up;
        }
      };
      if (p) sorting[p]();
      e.preventDefault();
      document
        .querySelectorAll(".tabs a")
        .forEach(e => e.classList.remove("active"));
      e.target.classList.add("active");
      let activeWidth = e.target.offsetWidth;
      let itemPos = e.target.offsetLeft;
      document.querySelector(".selector").style.left = itemPos + "px";
      document.querySelector(".selector").style.width = activeWidth + "px";
    },
    enterText: function() {
      var that = this;
      if (this.debounce_timer) {
        window.clearTimeout(this.debounce_timer);
      }
      this.debounce_timer = window.setTimeout(function() {
        that.searchChamp();
      }, 500);
    },
    searchChamp: function() {
      const champList = document.querySelectorAll(".champ-icon");
      // const champList = document.querySelectorAll("#champList .demo");

      champList.forEach(c => {
        if (c.dataset.name.toLowerCase().indexOf(this.inputSearch) > -1) {
          c.style.display = "";
        } else {
          c.style.display = "none";
        }
      });
    }
  }
};
</script>

